<template>
    <v-navigation-drawer
            persistent
            :mini-variant="miniVariant"
            :clipped="clipped"
            :temporary="temporary"
            @toggle="$store.state.sideMenu.active = !$store.state.sideMenu.active"
            v-model="$store.state.sideMenu.active"
            enable-resize-watcher
            fixed
            app
    >


                <v-layout
                >
                    <v-flex >
                        <v-card class="elevation-2" height="140px" >
                            <v-toolbar dark color="primary">
                                <v-toolbar-title>Marcus Taylor</v-toolbar-title>
                                <v-spacer></v-spacer>
                                <img src="../../assets/uc.png" width="40px" />
                            </v-toolbar>
                            <v-card-text class="mb-2">

                                marcus.taylor@cyberware.ltd.uk
                                <br />

                            </v-card-text>

                        </v-card>
                    </v-flex>
                </v-layout>

        <side-menu-items :drawer="drawer"/>
    </v-navigation-drawer>
</template>
<script>
  import SideMenuItems from "./SideMenuItems";

  export default {
    name: 'side-drawer',
    components: {SideMenuItems},
    props: {
      clipped: {},
      temporary: {},
      drawer: {},
      miniVariant: {}
    }
  }
</script>


