<template>
    <v-app>
        <side-drawer :clipped="clipped" :drawer="drawer" :mini-variant="miniVariant" :temporary="temporary"/>
        <v-toolbar
                app
                :clipped-left="clipped"
                color="primary"
                class="white--text"
        >
            <v-toolbar-side-icon dark @click="$store.state.sideMenu.active = !$store.state.sideMenu.active"></v-toolbar-side-icon>
            <v-toolbar-title v-text="title"></v-toolbar-title>
        </v-toolbar>
        <v-content>
            <router-view/>
        </v-content>

        <v-footer :fixed="fixed" app>
            <span>&copy; The world</span>
        </v-footer>
    </v-app>
</template>

<script>
  import SideDrawer from "./components/widgets/SideDrawer";

  export default {
    components: {SideDrawer},
    data() {
      return {
        clipped: false,
        temporary: true,
        drawer: false,
        fixed: false,
        miniVariant: false,
        title: 'UC Wireless'
      }
    },
    name: 'App'
  }
</script>



